import React from 'react';

const BuyMeACoffeeButton = () => {
  return (
    <div style={{ textAlign: 'center', fontFamily: 'cursive' }}>
      <strong>If you like my work, consider supporting me:</strong>

      <a
        href="https://buymeacoffee.com/zthanhluan"
        target="_blank"
        rel="noopener noreferrer"
        style={{
          display: 'flex',
          flexDirection: 'column',  // Align images vertically
          justifyContent: 'center', // Center vertically
          alignItems: 'center',     // Center horizontally
          textDecoration: 'none',   // Remove the underline from the link
        }}
      >
        <img
          src="/snapshot-bmc-button.png"
          alt="Buy Me a Coffee"
          style={{ width: '150px', height: 'auto', marginBottom: '10px', transition: 'transform 0.3s ease, box-shadow 0.3s ease', }} // Add some space between images
          className="zoom-shadow-effect"
        />
        <img
          src="/bmc_qr.png"
          alt="Buy Me a Coffee QR"
          style={{ width: '150px', height: 'auto' }}
        />
      </a>
      <style>
        {`
          .zoom-shadow-effect:hover {
            transform: scale(1.1); /* Zoom-in effect */
            box-shadow: 0 4px 20px rgba(0, 0, 0, 0.2); /* Shadow effect */
          }
        `}
      </style>
    </div>
  );
};

export default BuyMeACoffeeButton;