import React from "react";
import "./Footer.css";

const Footer = () => {
  return (
    <footer className="footer">
      <img src="/icon.png" width="24" height="24" alt="Logo" className="footer-logo" />
      &copy; {new Date().getFullYear()} by <span className="heart">❤</span>
    </footer>
  );
};

export default Footer;